const Ajv = require("ajv");

const ajv = new Ajv({
    allErrors: true, //
    jsonPointers: true,
    removeAdditional: true, //
    useDefaults: true, //
    coerceTypes: true //
});

ajv.addFormat("mobile", (value) => {
    const phone = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
    return phone.test(value);
});

require("ajv-errors")(ajv);

export default ajv;